import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http";
import { environment } from "environments/environment";
import { ICourses, IRequest,IsearchModel,RequestData } from 'app/admin/models/admin/course-request';

@Injectable({
  providedIn: 'root'
})
export class CourseRequestService {

  constructor(private http:HttpClient) { }
  // employee request
  getAllCourses<T>(){
    return this.http.get<ICourses>(`${environment.apiURL}CourseRequest/getAllCourses`);
  }
  addCourseRequest<T,Tv>(obj:T){
    return this.http.post<Tv>(`${environment.apiURL}CourseRequest/addCourseRequest`,obj);
  }
  getAllUserRequests<T>(page:number){
    return this.http.get<IRequest>(`${environment.apiURL}CourseRequest/getAllUserRequests/${page}`);
  }
  deleteRequest(request:RequestData){
    return this.http.delete(`${environment.apiURL}Admin/CourseRequest/deleteRequest/${request.id}`);

  }

    // admin 

  getAllRequest<T>(obj:IsearchModel){
    return this.http.post<IRequest>(`${environment.apiURL}Admin/CourseRequest/getAllRequests`,obj);
  }
  ApproveRequest(id:number,status:boolean){
    return this.http.get(`${environment.apiURL}Admin/CourseRequest/ApproveReQuest/${id}/${status}`);


  }
  


}
